<script>
import { eventBus } from '@/helpers/event-bus/'
import MainLayout from '@/layouts/MainLayout';
import Pencil from 'mdi-vue/Pencil'

import { createNamespacedHelpers } from 'vuex';
import {
    ACTION_CLIENT_ASK_PASSWORD,
    ACTION_CLIENT_UPDATE_SETTINGS,
    ACTION_UPDATE_CLIENT_DATA
} from '@/store/modules/client/action-types';

import {
    mapClientFields
} from '@/store/modules/client';

const {
    mapActions: mapClientActions
} = createNamespacedHelpers('client');

export default {
    components: {
        MainLayout,
        Pencil
    },
    data() {
        return {
            settingsBreadcrumbs: {
                title: 'Account Settings',
                steps: [
                    { url: '/', title: 'Home' },
                    { url: '/cp/account', title: 'Account' },
                    { url: 'cp/profile', title: 'Profile' }
                ],
                additionalClass: 'bc-wrapper bc-wrapper_user-account bc-wrapper--setting bc-right-bottom bc-horizontal'
            }
        }
    },
    computed: {
        ...mapClientFields([
            'client_data.firstname',
            'client_data.lastname',
            'client_data.email',
            'client_data.password',

            'client_data.address',
            'client_data.city',
            'client_data.zip',
            'client_data.country',
            'client_data.region',
            'client_data.phone_code',
            'client_data.phone',
            'client_data.phone_verified',

            'client_data.preferred_writers',
            'client_data.blocked_writers',
            'client_data.statistics',
            'client_data.chk_sms_notifications',
            'client_data.chk_email_notifications',
            'client_data.chk_email_marketing',
            'client_data.chk_calls',

            'client_data.aff_paymethod',
            'client_data.aff_paypal_email'

        ])
    },
    methods: {
        ...mapClientActions({
            askForPassword: ACTION_CLIENT_ASK_PASSWORD,
            updateSettings: ACTION_CLIENT_UPDATE_SETTINGS,
            updateClient: ACTION_UPDATE_CLIENT_DATA
        }),
        submit() {
            try {
                const response = this.updateSettings({
                    chk_sms_notifications: this.chk_sms_notifications === true ? 1 : 0,
                    chk_email_notifications: this.chk_email_notifications === true ? 1 : 0,
                    chk_email_marketing: this.chk_email_marketing === true ? 1 : 0,
                    chk_calls: this.chk_calls === true ? 1 : 0
                })
                if (response) {
                    eventBus.$emit('showSnackBar', 'Your settings have been saved', 'success');
                }
            } catch (err) {
                eventBus.$emit('showSnackBar', 'Failed to update settings', 'error');
            }
        }
    },
    beforeRouteEnter(to, from, next) {
        next(async (vm) => {
            await vm.updateClient()
        })
    }
}
</script>

